import moment, { Moment } from "moment";
import React from "react";
import { useAppDispatch } from "../../app/hooks";
import { getMeetingsByDateAsync } from "../meeting/meetingsSlice";
import { Calendar } from "./calendar";
import { CalendarWeek } from "./week";


type CalendarDayProps = {
    calendar: Calendar,
    week: CalendarWeek,
    day: Moment, 
};

export function CalendarDay(props: CalendarDayProps) {
    const dispatch = useAppDispatch();
   
    function openDay(): void {
        dispatch(getMeetingsByDateAsync(props.day))
    }

    function getDayClass(): string {
        var classes = ['day'];
        if(props.day.month() !== props.calendar.current.month()) {
          classes.push('other');
        } else if (props.calendar.today.isSame(props.day, 'day')) {
          classes.push('today');
        }
        return classes.join(' ');

    }

    return <div 
                 className={getDayClass()}
            onClick={() => openDay()}>
            {/* outer */}

            {/* name */}
            <div className="day-name">{props.day.format('ddd')}</div>

            {/* number */}
            <div className="day-number">{props.day.format('DD')}</div>
        </div>;
}