import axios from "axios";
import moment from "moment";
import { MeetingsItem } from "./meetingsSlice";

export async function getMonthStatistics(day: moment.Moment) {
  let res = await axios({
    url: `http://190.160.1.136:6789/get_month_times?month=${day.month() + 1}&year=${day.year()}`,
    method: 'get',
    timeout: 99999999,
  });

  return res.data;
}

export async function getMeetingsByDate(day: moment.Moment) {
  let res = 
   await axios({
    url: `http://190.160.1.136:6789/get_day_events?month=${day.month() + 1}&year=${day.year()}&day=${day.date()}`,
    method: 'get',
    timeout: 99999999,
    headers: { 
      'Access-Control-Allow-Origin' : '*',
    },
  });
  // const result: MeetingsItem[] = [{'name': 'Анти.Next', 'start_time': '2022-12-15 09:30:00+00:00', 'duration': 1.5, 'categories': null, 'description': 'Сбор докладов происходит здесь https://confluence.elcom.local/x/zuGaB.\r\n\r\nОбсуждение в чате дискорда #анти-dotnext<https://discord.gg/hYfKgr8C>\r\n\r\nПрограмма будет позже)\r\n\r\n', 'location': 'Комната переговоров Miami'}]; 
  const result: MeetingsItem[] = JSON.parse(res.data);
  return result;
}


