import { getByDisplayValue } from "@testing-library/react";
import moment from "moment";
import { Moment } from "moment";
import React from "react";
import { useAppDispatch } from "../../app/hooks";
import { Calendar } from "./calendar";
import { CalendarDay } from "./day";
import { CalendarWeek } from "./week";

type CalendarMonthProps = {
    calendar: Calendar
};

export class CalendarMonth extends React.Component<CalendarMonthProps, {}> {

    days: {[id: number]:  Moment[];} = {}
    start_week: number = 0

    render() {
        var self = this;

        self.backFill().map((day, i) => {
            if (day.day() === 0) {
                self.start_week += 1
                self.days[self.start_week] = []
            }

            self.days[self.start_week].push(day)
        })

        self.currentMonth().map((day, i) => {
            if (day.day() === 0) {
                self.start_week += 1
                self.days[self.start_week] = []
            }

            self.days[self.start_week].push(day)
        })

        self.fowardFill().map((day, i) => {
            if (day.day() === 0) {
                self.start_week += 1
                self.days[self.start_week] = []
            }

            self.days[self.start_week].push(day)
        })

        return <div className='month new'>
            {
                Object.keys(self.days).map((key, index) => ( 
                    <CalendarWeek key={index} calendar={self.props.calendar} days={self.days[index]} />
                ))
            }
            
        </div>;
    }

    backFill(): Array<Moment> {
        var days = Array<Moment>();

        var clone = this.props.calendar.current.clone();
        var dayOfWeek = clone.day();

        if(!dayOfWeek) { return days }

        clone.subtract('days', dayOfWeek+1);

        for(var i = dayOfWeek; i > 0 ; i--) {
            var nextday = moment(clone.add('days', 1))
            days.push(nextday);
        }

        return days
    }

    fowardFill(): Array<Moment> {
        var days = Array<Moment>();

        var clone = this.props.calendar.current.clone().add('months', 1).subtract('days', 1);
        var dayOfWeek = clone.day();

        if(dayOfWeek === 6) { return days }

        clone.subtract('days', dayOfWeek+1);

        for(var i = dayOfWeek; i < 6 ; i++) {
            days.push(moment(clone.add('days', 1)));
        }

        return days
    }

    currentMonth(): Array<Moment> {
        var days = Array<Moment>();

        var clone = this.props.calendar.current.clone();

        while(clone.month() === this.props.calendar.current.month()) {
            days.push(moment(clone));
            clone.add('days', 1);
        }
        return days
    }
}