import { createAsyncThunk, createSlice, PayloadAction } from '@reduxjs/toolkit';
import moment, { Moment } from 'moment';
import { RootState } from '../../app/store';
import { getMeetingsByDate } from './meetingsApi';

export interface MeetingsItem {
  name: string;
  start_time: string;
  duration: number;
  categories: string[] | null;
  description: string;
  location: string | null;
}



export interface MeetingsByDateState {
  events: MeetingsItem[];
  day: string;
  status: 'idle' | 'loading' | 'failed';
}

const initialState: MeetingsByDateState = {
  events: [],
  day: moment().format(),
  status: 'idle',
};

export const getMeetingsByDateAsync = createAsyncThunk(
  'meetings/getMeetingsByDateAsync',
  async (day: moment.Moment) => {
    const response = await getMeetingsByDate(day);
    return response;
  }
);

export const meetingsSlice = createSlice({
  name: 'meetings',
  initialState,
  reducers: {

  },
  extraReducers: (builder) => {
    builder
      .addCase(getMeetingsByDateAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(getMeetingsByDateAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.events = action.payload;
      })
      .addCase(getMeetingsByDateAsync.rejected, (state) => {
        state.status = 'failed';
      });
  },
});

export const selectEvents = (state: RootState) => state.meetings.events;

export const selectDay = (state: RootState) => state.meetings.day;

export default meetingsSlice.reducer;
