import moment from "moment";
import React from "react";
import { CalendarHeader } from "./header";
import { CalendarMonth } from "./month";
import { CalendarWeek } from "./week";


type CalendarProps = {
};

export class Calendar extends React.Component<CalendarProps, {}> {

    
    
    opened_events_week: CalendarWeek | null = null
    events = []
    statistics = {}
    history = {}

    current = moment().date(1);
    today = moment()

    prevMonth(): any {
        throw new Error("Method not implemented.");
    }

    nextMonth(): any {
        throw new Error("Method not implemented.");
    }

    add_events_week(week: CalendarWeek) {
        if (this.opened_events_week) {
            // this.opened_events_week

        }
        this.opened_events_week = week
    }
 

    render() {
        return <div id="calendar">
            <CalendarHeader calendar={this} />
            <CalendarMonth calendar={this} />
        </div>
    }
}
