import moment, { Moment } from "moment";
import React, { useState } from "react";
import { useAppDispatch } from "../../app/hooks";
import { MeetingsItem } from "./meetingsSlice";

type MeetingProps = {
    event: MeetingsItem
};

export class Meeting extends React.Component<MeetingProps, {}> {

    render() {

        return <div className="event">
            <div> {this.props.event.name} </div>
            <div> {this.props.event.start_time} </div>
            <div> {this.props.event.duration} </div>
            <div> {this.props.event.location} </div>
            <div> {this.props.event.description} </div>
        </div>;
    }

}